import React from "react";
import { Navigate, useLocation } from "react-router-dom";
import { useAuth } from "@/context/AuthContext";
import { usePermissions } from "@/context/PermissionContext";
// Removed Loading import - pages handle their own loading states
import { Alert, AlertDescription } from "@/components/ui/alert";
import { ShieldX } from "lucide-react";

interface ProtectedRouteProps {
  children: React.ReactNode;
  permission?: string;
  permissions?: string[];
  requireAll?: boolean;
  role?: string;
  roles?: string[];
  requireSuperAdmin?: boolean;
  redirectTo?: string;
  showError?: boolean;
}

const ProtectedRoute: React.FC<ProtectedRouteProps> = ({
  children,
  permission,
  permissions,
  requireAll = false,
  role,
  roles,
  requireSuperAdmin = false,
  redirectTo = "/login",
  showError = true,
}) => {
  const { isLoggedIn, loading: authLoading } = useAuth();
  const {
    hasPermission,
    hasAnyPermission,
    hasAllPermissions,
    role: userRole,
    isSuperAdmin,
    loading: permissionLoading,
  } = usePermissions();
  const location = useLocation();

  // Removed full-page loading - pages handle their own loading states with skeleton loading
  // If still loading auth/permissions, render children and let them handle loading

  // Redirect to login if not authenticated (only if auth check is complete)
  if (!authLoading && !isLoggedIn) {
    return <Navigate to={redirectTo} state={{ from: location }} replace />;
  }

  // If still loading, render children (they will show skeleton loading)
  if (authLoading || permissionLoading) {
    return <>{children}</>;
  }

  // Super admin check
  if (requireSuperAdmin && !isSuperAdmin) {
    if (showError) {
      return (
        <div className="flex items-center justify-center min-h-screen">
          <Alert variant="destructive" className="max-w-md">
            <ShieldX className="h-4 w-4" />
            <AlertDescription>
              Super admin privileges required to access this page.
            </AlertDescription>
          </Alert>
        </div>
      );
    }
    return <Navigate to="/dashboard" replace />;
  }

  // Role-based check
  if (role && (!userRole || userRole.name !== role)) {
    if (showError) {
      return (
        <div className="flex items-center justify-center min-h-screen">
          <Alert variant="destructive" className="max-w-md">
            <ShieldX className="h-4 w-4" />
            <AlertDescription>
              {`${role} role required to access this page.`}
            </AlertDescription>
          </Alert>
        </div>
      );
    }
    return <Navigate to="/dashboard" replace />;
  }

  if (roles && roles.length > 0 && (!userRole || !roles.includes(userRole.name))) {
    if (showError) {
      return (
        <div className="flex items-center justify-center min-h-screen">
          <Alert variant="destructive" className="max-w-md">
            <ShieldX className="h-4 w-4" />
            <AlertDescription>
              {`One of the following roles required: ${roles.join(", ")}`}
            </AlertDescription>
          </Alert>
        </div>
      );
    }
    return <Navigate to="/dashboard" replace />;
  }

  // Permission-based check
  if (permission && !hasPermission(permission)) {
    if (showError) {
      return (
        <div className="flex items-center justify-center min-h-screen">
          <Alert variant="destructive" className="max-w-md">
            <ShieldX className="h-4 w-4" />
            <AlertDescription>
              You don't have permission to access this page.
            </AlertDescription>
          </Alert>
        </div>
      );
    }
    return <Navigate to="/dashboard" replace />;
  }

  if (permissions && permissions.length > 0) {
    const hasRequiredPermissions = requireAll
      ? hasAllPermissions(permissions)
      : hasAnyPermission(permissions);

    if (!hasRequiredPermissions) {
      if (showError) {
        return (
          <div className="flex items-center justify-center min-h-screen">
            <Alert variant="destructive" className="max-w-md">
              <ShieldX className="h-4 w-4" />
              <AlertDescription>
                You don't have the required permissions to access this page.
              </AlertDescription>
            </Alert>
          </div>
        );
      }
      return <Navigate to="/dashboard" replace />;
    }
  }

  return <>{children}</>;
};

export default ProtectedRoute;
