import React, { useState } from "react";
import { useQuery } from "@tanstack/react-query";
import Page from "@/components/Page";
import Header from "@/components/Header";
// Removed Loading import - using skeleton loading instead
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Skeleton } from "@/components/ui/skeleton";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Key, Shield, Users, Settings, Plus, Edit, Trash2 } from "lucide-react";
import ProtectedRoute from "@/components/ProtectedRoute";
import RoleBasedButton from "@/components/RoleBasedButton";
import axiosInstance from "@/api/axios/axiosInstance";
import { toast } from "sonner";

interface Permission {
  id: number;
  name: string;
  display_name: string;
  description: string;
  category: string;
  resource: string;
  action: string;
  is_system_permission: boolean;
  is_active: boolean;
  created_at: string;
  updated_at: string;
}

const fetchPermissions = async (): Promise<Permission[]> => {
  const response = await axiosInstance.get("/api/permissions");
  return response.data.data;
};

const Permissions: React.FC = () => {
  const [searchQuery, setSearchQuery] = useState("");
  const [activeTab, setActiveTab] = useState("all");

  const { data: permissions, isLoading, error, refetch } = useQuery({
    queryKey: ["permissions"],
    queryFn: fetchPermissions,
  });

  const handleSearchChange = (value: string) => {
    setSearchQuery(value);
  };

  const handleDeletePermission = async (permissionId: number, permissionName: string) => {
    if (window.confirm(`Are you sure you want to delete the permission "${permissionName}"?`)) {
      try {
        await axiosInstance.delete(`/api/permissions/${permissionId}`);
        toast.success("Permission deleted successfully");
        refetch();
      } catch (error: any) {
        toast.error(error.response?.data?.message || "Failed to delete permission");
      }
    }
  };

  // Removed loading screen for better UX

  if (error) {
    console.error("Error fetching permissions", error);
    return <div>Error loading permissions</div>;
  }

  // Filter permissions based on search query and active tab
  let filteredPermissions = permissions?.filter((permission) =>
    permission.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
    permission.display_name.toLowerCase().includes(searchQuery.toLowerCase()) ||
    permission.description.toLowerCase().includes(searchQuery.toLowerCase()) ||
    permission.category.toLowerCase().includes(searchQuery.toLowerCase())
  ) || [];

  if (activeTab !== "all") {
    filteredPermissions = filteredPermissions.filter(permission => 
      permission.category.toLowerCase() === activeTab.toLowerCase()
    );
  }

  // Get unique categories
  const categories = [...new Set(permissions?.map(p => p.category) || [])];

  return (
    <ProtectedRoute requireSuperAdmin>
      <Page>
        <Header
          title="Permission Management"
          buttonText="Create Permission"
          filterByName={true}
          filterWord={searchQuery}
          onFilterChange={handleSearchChange}
          useSheet={false}
          showViewToggle={false}
          showFilters={false}
        />

        <div className="flex flex-col sm:flex-row items-start sm:items-center justify-between mb-6 gap-3">
          <div className="flex items-center space-x-2">
            <Badge variant="secondary" className="px-3 py-1">
              <Key className="h-3 w-3 mr-1" />
              <span>{filteredPermissions.length} Permissions</span>
            </Badge>
          </div>

          <RoleBasedButton
            requireSuperAdmin
            className="flex items-center gap-2"
          >
            <Plus className="h-4 w-4" />
            Create Permission
          </RoleBasedButton>
        </div>

        <div className="grid gap-6">
          {/* Permissions Overview Cards */}
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">Total Permissions</CardTitle>
                <Key className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{permissions?.length || 0}</div>
                <p className="text-xs text-muted-foreground">
                  All permissions
                </p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">System Permissions</CardTitle>
                <Settings className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">
                  {permissions?.filter(p => p.is_system_permission).length || 0}
                </div>
                <p className="text-xs text-muted-foreground">
                  Built-in permissions
                </p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">Custom Permissions</CardTitle>
                <Users className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">
                  {permissions?.filter(p => !p.is_system_permission).length || 0}
                </div>
                <p className="text-xs text-muted-foreground">
                  User-created permissions
                </p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">Categories</CardTitle>
                <Shield className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{categories.length}</div>
                <p className="text-xs text-muted-foreground">
                  Permission categories
                </p>
              </CardContent>
            </Card>
          </div>

          {/* Permissions Table with Tabs */}
          <Card>
            <CardHeader>
              <CardTitle>Permissions</CardTitle>
              <CardDescription>
                Manage system permissions and their assignments
              </CardDescription>
            </CardHeader>
            <CardContent>
              <Tabs value={activeTab} onValueChange={setActiveTab}>
                <TabsList className="grid w-full grid-cols-5">
                  <TabsTrigger value="all">All</TabsTrigger>
                  <TabsTrigger value="pages">Pages</TabsTrigger>
                  <TabsTrigger value="users">Users</TabsTrigger>
                  <TabsTrigger value="roles">Roles</TabsTrigger>
                  <TabsTrigger value="system">System</TabsTrigger>
                </TabsList>
                
                <TabsContent value={activeTab} className="mt-4">
                  <Table>
                    <TableHeader>
                      <TableRow>
                        <TableHead>Permission</TableHead>
                        <TableHead>Resource</TableHead>
                        <TableHead>Action</TableHead>
                        <TableHead>Category</TableHead>
                        <TableHead>Type</TableHead>
                        <TableHead>Actions</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {isLoading ? (
                        // Show skeleton loading for permissions
                        Array.from({ length: 8 }).map((_, index) => (
                          <TableRow key={`permission-skeleton-${index}`}>
                            <TableCell>
                              <div>
                                <Skeleton className="h-4 w-32 mb-1" />
                                <Skeleton className="h-3 w-24" />
                              </div>
                            </TableCell>
                            <TableCell>
                              <Skeleton className="h-6 w-16" />
                            </TableCell>
                            <TableCell>
                              <Skeleton className="h-6 w-16" />
                            </TableCell>
                            <TableCell>
                              <Skeleton className="h-4 w-20" />
                            </TableCell>
                            <TableCell>
                              <Skeleton className="h-6 w-20" />
                            </TableCell>
                            <TableCell>
                              <div className="flex items-center gap-2">
                                <Skeleton className="h-8 w-8" />
                                <Skeleton className="h-8 w-8" />
                              </div>
                            </TableCell>
                          </TableRow>
                        ))
                      ) : (
                        filteredPermissions.map((permission) => (
                        <TableRow key={permission.id}>
                          <TableCell>
                            <div>
                              <div className="font-medium">{permission.display_name}</div>
                              <div className="text-sm text-muted-foreground">{permission.name}</div>
                            </div>
                          </TableCell>
                          <TableCell>
                            <Badge variant="outline">{permission.resource}</Badge>
                          </TableCell>
                          <TableCell>
                            <Badge variant="secondary">{permission.action}</Badge>
                          </TableCell>
                          <TableCell>
                            <Badge variant="default">{permission.category}</Badge>
                          </TableCell>
                          <TableCell>
                            <Badge variant={permission.is_system_permission ? "secondary" : "default"}>
                              {permission.is_system_permission ? "System" : "Custom"}
                            </Badge>
                          </TableCell>
                          <TableCell>
                            <div className="flex items-center gap-2">
                              <RoleBasedButton
                                requireSuperAdmin
                                size="sm"
                                variant="outline"
                              >
                                <Edit className="h-3 w-3" />
                              </RoleBasedButton>
                              
                              {!permission.is_system_permission && (
                                <RoleBasedButton
                                  requireSuperAdmin
                                  size="sm"
                                  variant="outline"
                                  onClick={() => handleDeletePermission(permission.id, permission.display_name)}
                                >
                                  <Trash2 className="h-3 w-3" />
                                </RoleBasedButton>
                              )}
                            </div>
                          </TableCell>
                        </TableRow>
                      ))
                      )}
                    </TableBody>
                  </Table>
                </TabsContent>
              </Tabs>
            </CardContent>
          </Card>
        </div>
      </Page>
    </ProtectedRoute>
  );
};

export default Permissions;
