import React from "react";
import { usePermissions } from "@/context/PermissionContext";
import { Button } from "@/components/ui/button";
import { ButtonProps } from "@/components/ui/button";

interface RoleBasedButtonProps extends ButtonProps {
  permission?: string;
  permissions?: string[];
  requireAll?: boolean;
  role?: string;
  roles?: string[];
  requireSuperAdmin?: boolean;
  fallback?: React.ReactNode;
  hideWhenNoAccess?: boolean;
}

const RoleBasedButton: React.FC<RoleBasedButtonProps> = ({
  children,
  permission,
  permissions,
  requireAll = false,
  role,
  roles,
  requireSuperAdmin = false,
  fallback,
  hideWhenNoAccess = false,
  disabled,
  ...buttonProps
}) => {
  const {
    hasPermission,
    hasAnyPermission,
    hasAllPermissions,
    role: userRole,
    isSuperAdmin,
    loading,
  } = usePermissions();

  if (loading) {
    return (
      <Button disabled {...buttonProps}>
        <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-gray-300"></div>
      </Button>
    );
  }

  // Check access permissions
  let hasAccess = true;

  // Super admin check
  if (requireSuperAdmin && !isSuperAdmin) {
    hasAccess = false;
  }

  // Role-based check
  if (hasAccess && role && (!userRole || userRole.name !== role)) {
    hasAccess = false;
  }

  if (hasAccess && roles && roles.length > 0 && (!userRole || !roles.includes(userRole.name))) {
    hasAccess = false;
  }

  // Permission-based check
  if (hasAccess && permission && !hasPermission(permission)) {
    hasAccess = false;
  }

  if (hasAccess && permissions && permissions.length > 0) {
    const hasRequiredPermissions = requireAll
      ? hasAllPermissions(permissions)
      : hasAnyPermission(permissions);

    if (!hasRequiredPermissions) {
      hasAccess = false;
    }
  }

  // If no access and should hide, return null
  if (!hasAccess && hideWhenNoAccess) {
    return null;
  }

  // If no access and fallback provided, return fallback
  if (!hasAccess && fallback) {
    return <>{fallback}</>;
  }

  // Return button with appropriate disabled state
  return (
    <Button disabled={disabled || !hasAccess} {...buttonProps}>
      {children}
    </Button>
  );
};

export default RoleBasedButton;
